"use client";

import { useState } from 'react';
import { MenuIcon, CloseIcon } from './Icons';

interface MobileMenuProps {
  links: {
    href: string;
    label: string;
  }[];
}

const MobileMenu: React.FC<MobileMenuProps> = ({ links }) => {
  const [isOpen, setIsOpen] = useState(false);

  const toggleMenu = () => {
    setIsOpen(!isOpen);
  };

  return (
    <div className="md:hidden">
      <button 
        onClick={toggleMenu}
        className="text-gray-600 hover:text-blue-600 transition"
        aria-label={isOpen ? "Close menu" : "Open menu"}
      >
        {isOpen ? <CloseIcon /> : <MenuIcon />}
      </button>

      {isOpen && (
        <div className="absolute top-16 inset-x-0 z-50 bg-white border-b border-gray-100 shadow-lg">
          <div className="px-4 py-6 space-y-4">
            {links.map((link, index) => (
              <a
                key={index}
                href={link.href}
                className="block text-lg py-2 text-gray-600 hover:text-blue-600 transition"
                onClick={() => setIsOpen(false)}
              >
                {link.label}
              </a>
            ))}
            <a 
              href="/signup" 
              className="block w-full text-center rounded-full bg-blue-600 px-5 py-2 text-white font-medium hover:bg-blue-700 transition mt-6"
              onClick={() => setIsOpen(false)}
            >
              Get Started
            </a>
          </div>
        </div>
      )}
    </div>
  );
};

export default MobileMenu;